import React from 'react';
import styles from './List.module.scss'
import { getArticleList } from '../../api/ArticlesAPI';
import { IArticle } from '../../types/index';
import ListItem from './ListItem';
import { Select } from 'antd';
import { placeArr } from '../../utils';

export default function List() {
  const [list, setList] = React.useState<Array<IArticle>>([]);
  React.useEffect(() => {
    getArticleList().then((data: any) => {
      setList(data);
    });
  }, []);
  const { Option } = Select;
  const onSearch = () => {
    debugger
  }
  function onPlaceChange(val: any) {
    debugger
  }

  return (
    <div id="home-list" className={styles.homeList}>
      <div className={styles.searchContainer}>
        <Select showSearch onChange={onPlaceChange} style={{ width: 200 }} placeholder="选择地点关键词">
          {
            placeArr.map((val, index) => {
              return <Option key={index} value={val}>{val}</Option>
            })
          }
        </Select>
      </div>
      <div className={styles.listContainer}>
        {
          list.map((item) => {
            return <ListItem key={item.link} article={item} />
          })
        }
      </div>
    </div>
  );
}
