import React from 'react';
import styles from './ListItem.module.scss';
import { IArticle } from '../../types';
import { ALT_IMAGE_URL } from '../../utils';
import { Tooltip, Tag, Select } from 'antd';
type ListItemProps = {
  article: IArticle,
};

export default function ListItem({
  article
}: ListItemProps) {

  React.useEffect(() => {
  }, []);

  const imgSrc = article.imgLinks.length ? `${article.imgLinks[0]}` : `${ALT_IMAGE_URL}`
  const colors = ['#FE661D', '#28DA9C', '#00AAE4', '#DF4EC2', '#78C100', '#EC322F', '#19B629', '#943CFF'];


  return (
    <div className={styles.listItem} >
      <a href={article.link} className={styles.card}>
        <div className={styles.imgContainer}>
          <img src={imgSrc} />
          {
            article.imgLinks.length ? '' : <span className={styles.emptyText}>暂无图片</span>
          }
        </div>
        <div className={styles.textContainer}>
          <Tooltip title={article.isEliteArticle ? `精华帖 - ${article.title}` : article.title}>
            <span className={styles.title}>{article.isEliteArticle ? <i className='icon-heart'> </i> : ''}{`《${article.title}》`}</span>
          </Tooltip>
          <Tooltip title={`作者：${article.author}`}>
            <img className={styles.authorPic} src={article.authorPic} />
            <span className={styles.author}>{article.author}</span>
          </Tooltip>

          <div className={styles.dataContainer}>
            <Tooltip className={styles.dataItem} title={`文章内容长度：${article.contentLength}`}>
              <i className={`icon-contentLength`}></i>
              <span className={styles.text}>{article.contentLength}</span>
            </Tooltip>
            <Tooltip className={styles.dataItem} title={`照片数量：${article.imgLinks.length}`}>
              <i className={`icon-photo`}></i>
              <span className={styles.text}>{article.imgLinks.length}</span>
            </Tooltip>
            <Tooltip className={styles.dataItem} title={`评论数：${article.responseCount}`}>
              <i className={`icon-response`}></i>
              <span className={styles.text}>{article.responseCount}</span>
            </Tooltip>
          </div>
          <div className={styles.dataContainer}>
            <Tooltip className={styles.dataItem} title={`作者回复数：${article.authorResponse}`}>
              <i className={`icon-authorResponse`}></i>
              <span className={styles.text}>{article.authorResponse}</span>
            </Tooltip>
            <Tooltip className={styles.dataItem} title={`点赞数：${article.likeCount}`}>
              <i className={`icon-like`}></i>
              <span className={styles.text}>{article.likeCount}</span>
            </Tooltip>
            <Tooltip className={styles.dataItem} title={`收藏数：${article.favoriteCount}`}>
              <i className={`icon-favorite`}></i>
              <span className={styles.text}>{article.favoriteCount}</span>
            </Tooltip>
          </div>
          <div className={styles.dataContainer}>
            <Tooltip className={styles.keyWordContainer} title={`关键词：${article.keyWords.join('，')}`}>
              {
                article.keyWords.map((keyWord, keyIndex) => {
                  return <Tag key={keyIndex} color={colors[keyIndex % 8]}>{keyWord}</Tag>
                })
              }
              <span></span>
            </Tooltip>
          </div>
          <div className={`${styles.dataContainer} ${styles.publishTimeContainer}`}>
            <Tooltip title={`发布时间：${article.lastResponse}`}>
              <i className={`icon-new`}></i>
              <span className={styles.publishTime}>{article.lastResponse}</span>
            </Tooltip>

          </div>
        </div>
      </a >
    </div >
  );
}

